import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

const routes = [
    {
      path: '/recorderDemo',
      name: 'recorderDemo',
      component: () => import('@/views/recorderDemo')
    },
    {
      path: '/pdf',
      name: 'pdf',
      component: () => import('@/views/pdf')
    }
  ]

  export default new VueRouter({
    routes
  })