<template>
  <div>
    <div id="xigua"></div>
  </div>
</template>

<script>
export default {
  name: 'nsVideo',
  props: {
    src: {
        type: String,
        default: 'https://media.w3.org/2010/05/sintel/trailer.mp4'
    },
    autoplay: { // 设置/返回 自动播放属性
        type: Boolean,
        default: false
    },
    disableProgress: { // 是否禁止滚动条拖拽
        type: Boolean,
        default: false
    },
  },
  data(){
    return {
        player: null,
    }
  },
  created(){},
  mounted(){
    this.player = new window.Player({
      id: 'xigua',
      url: 'https://media.w3.org/2010/05/sintel/trailer.mp4',
      autoplay: true,
      disableProgress: true,
      allowSeekPlayed: true,
      // rotateFullscreen: true,
      // whitelist: ['']
    });
    this.$emit('player', this.player);
    this.player.on('requestFullscreen',()=>{
      console.log('全屏了');
      this.player.pause()
    })
    this.player.on('exitFullscreen',()=>{
      this.player.play()
    })
  },
  methods:{}
}
</script>

